table{
  background-color: rgb(251, 159, 213);
  margin: auto;
  border: 1px solid black;
}
td,tr{
  border: 1px solid black;
}
button{
  margin-left: 5px;
  cursor:pointer;
  color: purple;
}
.buttons{
  text-decoration: none;
  color: black;
  background-color:white;
  border: 1px solid black;
}
body{
  background-color: rgb(245, 211, 222);
  height: 2800px;
  width: 100%;
  cursor: pointer;
}
h1 {
  text-align: center;
    border-color: rgb(14, 14, 14);
    cursor: pointer;
    border-radius: 20px;
    margin-top: 20px;
    transition: 0.3s;
    padding: 12px 16px;
    background-color: rgb(253, 184, 226);
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(251, 10, 159);
    font-weight: bold;
}